"use client";

import type React from "react";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";

import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import {
  ArrowLeft,
  Search,
  Plus,
  Upload,
  Trash2,
  Eye,
  FileDown,
} from "lucide-react";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { useToast } from "@/hooks/use-toast";
import { Toaster } from "@/components/ui/toaster";
import * as XLSX from "xlsx";

type Student = {
  id: string;
  name: string;
  rollNumber: string;
  email: string;
  year: "I" | "II" | "III";
  department: string;
  dob: string;
  phone: string;
  batch: string;
};

type BulkUploadError = {
  row: number;
  error: string;
};

export default function HODStudentsPage() {
  const router = useRouter();
  const { toast } = useToast();
  const { data: session, status } = useSession();
  const [user, setUser] = useState<any>(null);
  const [students, setStudents] = useState<Student[]>([]);
  const [filteredStudents, setFilteredStudents] = useState<Student[]>([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [yearFilter, setYearFilter] = useState<string>("all");
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [formData, setFormData] = useState({
    name: "",
    rollNumber: "",
    email: "",
    year: "I" as "I" | "II" | "III",
    department: "",
    dob: "",
    phone: "",
    batch: "",
  });
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [studentToDelete, setStudentToDelete] = useState<string | null>(null);

  // Bulk Upload State
  const [isBulkUploadOpen, setIsBulkUploadOpen] = useState(false);
  const [bulkUploadErrors, setBulkUploadErrors] = useState<BulkUploadError[]>(
    []
  );
  const [parsedStudents, setParsedStudents] = useState<any[]>([]);

  useEffect(() => {
    if (status === "loading") return;
    if (status === "unauthenticated") {
      router.push("/");
      return;
    }

    const userData = session?.user;
    if (!userData) return;

    if (userData.role?.toLowerCase() !== "hod") {
      router.push(`/dashboard/${userData.role?.toLowerCase()}`);
      return;
    }

    setUser(userData);

    const storedStudents = localStorage.getItem("students");
    if (storedStudents) {
      const parsedStudents = JSON.parse(storedStudents);
      setStudents(parsedStudents);
      setFilteredStudents(parsedStudents);
    }
  }, [status, session, router]);

  useEffect(() => {
    let filtered = students;

    if (searchQuery) {
      filtered = filtered.filter(
        (student) =>
          student.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
          student.rollNumber.toLowerCase().includes(searchQuery.toLowerCase())
      );
    }

    if (yearFilter !== "all") {
      filtered = filtered.filter((student) => student.year === yearFilter);
      filtered.sort((a, b) => {
        const rollA = parseInt(a.rollNumber.slice(-3)) || 0;
        const rollB = parseInt(b.rollNumber.slice(-3)) || 0;
        return rollA - rollB;
      });
    }

    setFilteredStudents(filtered);
  }, [searchQuery, yearFilter, students]);

  const handleAddStudent = (e: React.FormEvent) => {
    e.preventDefault();

    if (students.some((s) => s.rollNumber === formData.rollNumber)) {
      toast({
        title: "Duplicate Student",
        description: `A student with Roll Number ${formData.rollNumber} already exists.`,
        variant: "destructive",
      });
      return;
    }

    const newStudent: Student = {
      id: Date.now().toString(),
      ...formData,
    };

    const updatedStudents = [...students, newStudent];
    setStudents(updatedStudents);
    localStorage.setItem("students", JSON.stringify(updatedStudents));

    const studentLogins = JSON.parse(
      localStorage.getItem("studentLogins") || "{}"
    );
    studentLogins[formData.rollNumber] = {
      password: formData.dob,
      role: "student",
      name: formData.name,
      studentId: newStudent.id,
    };
    localStorage.setItem("studentLogins", JSON.stringify(studentLogins));

    toast({
      title: "Success",
      description: "Student added successfully.",
    });

    setFormData({
      name: "",
      rollNumber: "",
      email: "",
      year: "I",
      department: "",
      dob: "",
      phone: "",
      batch: "",
    });
    setIsDialogOpen(false);
  };

  const handleDeleteStudent = () => {
    if (!studentToDelete) return;

    const updatedStudents = students.filter((s) => s.id !== studentToDelete);
    setStudents(updatedStudents);
    localStorage.setItem("students", JSON.stringify(updatedStudents));

    const student = students.find((s) => s.id === studentToDelete);
    if (student) {
      const studentLogins = JSON.parse(
        localStorage.getItem("studentLogins") || "{}"
      );
      delete studentLogins[student.rollNumber];
      localStorage.setItem("studentLogins", JSON.stringify(studentLogins));
    }

    toast({
      title: "Student Deleted",
      description: "Student record has been permanently removed.",
    });

    setDeleteDialogOpen(false);
    setStudentToDelete(null);
  };

  const handleDownloadTemplate = () => {
    const templateData = [
      {
        Name: "John Doe",
        RollNumber: "AI2023001",
        Email: "john@example.com",
        Phone: "9876543210",
        Year: "I",
        Department: "AI & DS",
        Batch: "2023-2027",
        DOB: "2005-01-01",
      },
    ];

    const ws = XLSX.utils.json_to_sheet(templateData);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "Students");
    XLSX.writeFile(wb, "Student_Upload_Template.xlsx");
  };

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (evt) => {
      const bstr = evt.target?.result;
      const wb = XLSX.read(bstr, { type: "binary" });
      const wsname = wb.SheetNames[0];
      const ws = wb.Sheets[wsname];
      const data = XLSX.utils.sheet_to_json(ws);

      const errors: BulkUploadError[] = [];
      const validStudents: any[] = [];

      data.forEach((row, index) => {
        const rowNum = index + 2;
        if (!row.Name || !row.RollNumber || !row.Email) {
          errors.push({ row: rowNum, error: "Missing required fields" });
          return;
        }
        validStudents.push(row);
      });

      setBulkUploadErrors(errors);
      setParsedStudents(validStudents);
    };
    reader.readAsBinaryString(file);
  };

  const handleBulkImport = () => {
    const newStudents: Student[] = parsedStudents.map((row) => ({
      id: Date.now().toString(),
      name: row.Name,
      rollNumber: row.RollNumber,
      email: row.Email,
      year: row.Year,
      department: row.Department,
      dob: row.DOB,
      phone: row.Phone,
      batch: row.Batch,
    }));

    const updatedStudents = [...students, ...newStudents];
    setStudents(updatedStudents);
    localStorage.setItem("students", JSON.stringify(updatedStudents));

    toast({
      title: "Success",
      description: `Successfully imported ${newStudents.length} students.`,
    });

    setIsBulkUploadOpen(false);
    setParsedStudents([]);
    setBulkUploadErrors([]);
  };

  return (
    <div className="min-h-screen bg-background">
      <header className="border-b bg-card">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center gap-4">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => router.push("/dashboard/hod")}
            >
              <ArrowLeft className="h-4 w-4 mr-2" />
              Back to Dashboard
            </Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        <div className="mb-8 flex items-center justify-between">
          <div>
            <h2 className="text-3xl font-semibold tracking-tight mb-2">
              Student Management
            </h2>
            <p className="text-muted-foreground">
              View and manage all students
            </p>
          </div>
          <div className="flex gap-2">
            <Dialog open={isBulkUploadOpen} onOpenChange={setIsBulkUploadOpen}>
              <DialogTrigger asChild>
                <Button variant="outline">
                  <Upload className="h-4 w-4 mr-2" />
                  Bulk Upload
                </Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>Bulk Upload Students</DialogTitle>
                  <DialogDescription>
                    Upload an Excel file to add multiple students at once.
                  </DialogDescription>
                </DialogHeader>
                <div>
                  <Button onClick={handleDownloadTemplate}>
                    Download Template
                  </Button>
                  <Input type="file" onChange={handleFileUpload} />
                  {bulkUploadErrors.length > 0 && (
                    <div>
                      <p>Errors:</p>
                      <ul>
                        {bulkUploadErrors.map((err, i) => (
                          <li key={i}>
                            Row {err.row}: {err.error}
                          </li>
                        ))}
                      </ul>
                    </div>
                  )}
                  <Button onClick={handleBulkImport}>Import Students</Button>
                </div>
              </DialogContent>
            </Dialog>
            <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
              <DialogTrigger asChild>
                <Button>
                  <Plus className="h-4 w-4 mr-2" />
                  Add Student
                </Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>Add New Student</DialogTitle>
                  <DialogDescription>
                    Enter student details to add them to the system.
                  </DialogDescription>
                </DialogHeader>
                <form onSubmit={handleAddStudent}>
                  <Input
                    placeholder="Name"
                    value={formData.name}
                    onChange={(e) =>
                      setFormData({ ...formData, name: e.target.value })
                    }
                  />
                  <Button type="submit">Add</Button>
                </form>
              </DialogContent>
            </Dialog>
          </div>
        </div>
        <Card>
          <CardHeader>
            <Input
              placeholder="Search..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Roll Number</TableHead>
                  <TableHead>Name</TableHead>
                  <TableHead>Year</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filteredStudents.map((student) => (
                  <TableRow key={student.id}>
                    <TableCell>{student.rollNumber}</TableCell>
                    <TableCell>{student.name}</TableCell>
                    <TableCell>{student.year}</TableCell>
                    <TableCell>
                      <Button onClick={() => setStudentToDelete(student.id)}>
                        Delete
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </main>
      <Toaster />
    </div>
  );
}
